body, html {
  -webkit-font-smoothing: antialiased;
  margin: 0;
  padding: 0;
  background-color: #f1f1f1;
  font-family: 'Raleway', sans-serif;
  -webkit-text-size-adjust: 100%;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 1rem/16;
  margin-top: 50px;
}

li, ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul {
  margin-top: 10px;
}

li {
  font-size: 0.8rem;
  margin-bottom: 8px;
  text-align: center;
  color: #959595;
}

li:last-of-type {
  margin-bottom: 50px;
}

.Card {
  font-size: 1.5rem;
  font-weight: bold;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  width: 200px;
  border-radius: 10px;
  background-color: white;
  -webkit-box-shadow: 0 5px 3px 0 #ebebeb;
          box-shadow: 0 5px 3px 0 #ebebeb;
}

.header {
  position: relative;
  font-size: 20px;
  margin: 12px 0;
  color: #575757;
}

.header::after {
  content: '';
  position: absolute;
  left: -50%;
  bottom: -10px;
  width: 200%;
  height: 1px;
  background-color: #f1f1f1;
}

.searchBar {
  text-align: center;
  margin: 5px 0;
  border: 1px solid #c4c4c4;
  height: 20px;
  color: #575757;
  border-radius: 3px;
}

.searchBar:focus {
  outline-width: 0;
}

.searchBar::-webkit-input-placeholder {
  color: #dadada;
}

.searchBar:-ms-input-placeholder {
  color: #dadada;
}

.searchBar::-ms-input-placeholder {
  color: #dadada;
}

.searchBar::placeholder {
  color: #dadada;
}

.isLoading {
  margin: 30px 0;
  width: 150px;
  -webkit-filter: opacity(0.3);
          filter: opacity(0.3);
}



